<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>{{ Title }}</h3>
        </div>
        <Lessons :key="$route.params.id" :loader="loadData" :tag="Title" />
      </div>
    </div>
  </div>
</template>
<script>
import Lessons from "@/components/Lessons.vue";
export default {
  components: {
    Lessons,
  },
  data() {
    return {
      Title: "搜尋結果",
      LessonCatrgories: [],
    };
  },
  async mounted() {
    this.LessonCatrgories = await this.$api.getLessonCategories();
    this.load();
  },
  methods: {
    load() {
      this.Title = "搜尋結果：" + this.$route.params.id;
    },
    loadData() {     
      return this.$api.searchLesson(this.$route.params.id);
    },
  },
  watch: {
    $route() {
      this.load();
    },
  },
};
</script>