<template>
  <div class="historyList">
    <div class="historyListItem" v-for="l in Lessons" :key="l.LessonID">
      <router-link :to="`/lesson/${l.LessonID}`">
        <div class="courseInfo">
          <h3>
            <b>{{ l.LessonName }}</b>
          </h3>
          <p v-plaintext v-if="!hideBrief">
            {{ l.Brief }}
          </p>
        </div>
        <div class="courseImg">
          <img :src="l.ImageUrl | imageCDN" />
          <div class="cateTag" v-if="l.LessonCategoryID == 1">師培課程<img src="/img/play2.svg" /></div>
          <div class="cateTag" v-else>學生課程<img src="/img/play2.svg" /></div>
          <div class="time">{{ l.VideoLength | chhmmss }}</div>
          <router-link v-if="showContinue" :to="`/lesson/${l.LessonID}?play=1`"><div class="continue">繼續上課</div></router-link>
        </div></router-link
      >
    </div>
  </div>
</template>
<script>
export default {
  props: ["loader", "tag", "hideBrief", "showContinue"],
  data() {
    return {
      Lessons: [],
    };
  },
  async mounted() {
    this.Lessons = await this.loader();
  },
};
</script>